<template>
  <div class="main">
    <el-row :gutter="20">
      <el-col :span="12">
        <div class="leftMain">
          <el-button type="primary" @click="postLoan">申请贷款</el-button>
          <el-button type="success">提前还款</el-button>
          <el-button type="danger" @click="getLoanSchedule">贷款申请进度查询</el-button>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="rightMain">
          <el-row :gutter="20">
            <el-col :span="12">
              <div class="col6">
                <el-button type="green">首带申请流程</el-button>
                <el-button type="warning">续待帮助</el-button>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="col6">
                <el-button type="green">常见问题</el-button>
                <el-button type="warning">提前还款帮助</el-button>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <div class="bottom">
      <el-row :gutter="20">
        <el-col :span="12">
          <el-card class="box-card">
            <div class="cardLeft">
              <i class="el-icon-message-solid" />
            </div>
            <div class="cardRight">
              <div class="cardTitle">贷款提醒</div>
              <div>如果您需要申请贷款，请查看生源地贷款申请流程</div>
              <div>贷款办理时间：</div>
              <div>必须在指定的时间内才能申请助学贷款，一般为每年6月-9月，具体以当地县区资助中心办理时间为准，请随时关注。</div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card class="box-card">
            <div class="cardLeft">
              <i class="el-icon-alarm-clock" />
            </div>
            <div class="cardRight">
              <div class="cardTitle">征信提醒</div>
              <div>您的贷款、还款记录已报送人民银行个人征信系统，请务必按时还款。</div>
              <div>如有违约，会影响您的信用记录，有关不良记录将保留至逾期贷款结清后5年，今后在办理信用卡、申请房贷与车贷等其它各类贷款时会有不良影响。</div>
              <div>请珍惜您的信用记录，及时还款。</div>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row :gutter="20" style="margin-top:2rem">
        <el-col :span="12">
          <el-card class="box-card">
            <div class="cardLeft">
              <i class="el-icon-date" />
            </div>
            <div class="cardRight">
              <div class="cardTitle">还款提醒</div>
              <div style="color:#ff0000">目前助学贷款还款方式仅支持支付宝和POS机还款，根据监管要求，还款后请及时查看还款情况。</div>
              <div>正常还款日：每年11月1日至12月20日(最后一年为9月1日至20日)。</div>
              <div>提前还款日：一般每月15日(含)之前提交申请，利息计算至当月20日，请与当月20日前还款；16日以后提交申请，利息计算至次月20日，请于次月20日前还款；其他特殊情况以合同约定为准。</div>
              <div>逾期还款日：1月至10月的1日至20日，11月1日至12月20日，可以进行逾期还款。</div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card class="box-card">
            <div class="cardLeft">
              <i class="el-icon-search" />
            </div>
            <div class="cardRight">
              <div class="cardTitle">本人所属资助中心联系方式</div>
              <el-button v-if="!display" type="danger" @click="display = true">查看</el-button>
              <div v-else>
                <div>资助中心名称:锦屏县学生资助管理办公室</div>
                <div>资助中心地址:贵州省黔东南州锦屏县学生资助管理中心</div>
                <div>邮政编码:556700 QQ:</div>
                <div>联系人:李双莲</div>
                <div>联系电话:08557221310</div>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <el-dialog
      title="请填写贷款信息-贷款基本信息"
      :visible.sync="dialogVisible"
      width="780px"
      style="text-align:left"
    >
      <el-form
        inline
        :model="LoanAppForm"
        label-width="130px"
        ref="Form"
        label-position="right"
        :rules="rules"
      >
        <el-form-item label="姓名">
          <el-input size="small" v-model="LoanAppForm.userName" disabled></el-input>
        </el-form-item>
        <el-form-item label="申请学年">
          <el-input size="small" v-model="LoanAppForm.applicationYear" disabled></el-input>
        </el-form-item>
        <el-form-item label="资助中心">
          <el-input
            style="width:540px"
            size="small"
            v-model="LoanAppForm.fundingCenterName"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="贷款金额">
          <el-input size="small" type="number" v-model="LoanAppForm.loanMoney"></el-input>
        </el-form-item>
        <el-form-item label="贷款年限">
          <el-input-number
            size="small"
            v-model="LoanAppForm.loanTerm"
            controls-position="right"
            :min="1"
            :max="10"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="申请原因">
          <el-input
            style="width:540px"
            maxlength="50"
            show-word-limit
            type="textarea"
            size="small"
            v-model="LoanAppForm.loanReason"
          ></el-input>
        </el-form-item>
        <el-form-item :label="LoanAppForm.userLoanType+'声明'">
          <el-input
            style="width:540px"
            maxlength="50"
            show-word-limit
            size="small"
            type="textarea"
            v-model="LoanAppForm.loanStatement"
          ></el-input>
        </el-form-item>
        <el-form-item style="padding-left: 130px;">
          <el-button type="primary" @click="onSubmit">提交</el-button>
          <el-button @click="dialogVisible = false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog
      title="贷款申请进度查询"
      :visible.sync="dialogVisible2"
      width="780px"
      style="text-align:left"
    >
      <el-steps :active="stepsActive" finish-status="success" simple>
        <el-step title="导出申请表 " icon="el-icon-download"></el-step>
        <el-step title="管理员审核" icon="el-icon-s-check"></el-step>
        <el-step title="完成贷款" icon="el-icon-circle-check"></el-step>
      </el-steps>
      <div v-if="stepsActive == 0" class="stepContent">
        <el-alert
          title="恭喜您贷款申请成功！"
          description="请将申请表导出打印，并提交给管理员，管理员将进行审核。"
          type="success"
          :closable="false"
          style="margin-bottom:20px"
          show-icon
        ></el-alert>
        <el-button
          slot="reference"
          type="success"
          @click="exportFile"
          plain
          style="margin-right:10px"
        >导出贷款申请表</el-button>
        <el-popconfirm
          confirmButtonText="确定"
          cancelButtonText="还不确定"
          icon="el-icon-info"
          iconColor="red"
          title="您确定已将申请表提交给管理员了吗？"
          @onConfirm="onConfirm"
        >
          <el-button slot="reference" type="primary" plain>下一步</el-button>
        </el-popconfirm>
      </div>
      <div v-if="stepsActive == 1" class="stepContent">
        <div class="two">
          <i class="el-icon-loading"></i>
          请耐心等待管理员审核...
        </div>
      </div>
      <div v-if="stepsActive == 2" class="stepContent">
        <div class="two">
          <i class="el-icon-success"></i>
          恭喜你,已经完成了贷款!
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userName: "",
      display: false,
      dialogVisible: false,
      dialogVisible2: false,
      stepsActive: 0,
      contractId: "",
      LoanAppForm: {
        userLoanType: "",
        userName: "",
        appcationYear: "",
        loanMoney: "",
        loanTerm: "",
        loanReason: "",
        loanStatement: ""
      },
      rules: {
        loanMoney: [
          { required: true, message: "请填写贷款金额", trigger: "blur" }
        ],
        loanTerm: [
          { required: true, message: "请填写贷款年限", trigger: "blur" }
        ],
        loanReason: [
          { required: true, message: "请填写贷款原因", trigger: "blur" }
        ],
        loanStatement: [
          { required: true, message: "请填写贷款声明", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    postLoan() {
      this.$axios
        .post("/loan/getStudentInfo", {
          userLoginAccount: sessionStorage.getItem("userLoginAccount")
        })
        .then(({ status, data }) => {
          if (status == 200) {
            this.dialogVisible = true;
            this.LoanAppForm = data;
          } else if (status == 401) {
            this.$message({
              message: "获取失败",
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    onSubmit() {
      this.$refs.Form.validate(valid => {
        if (valid) {
          this.$axios
            .post("/loan/fillLoanAppForm", {
              userLoginAccount: sessionStorage.getItem("userLoginAccount"),
              loanMoney: this.LoanAppForm.loanMoney,
              loanTerm: this.LoanAppForm.loanTerm,
              loanReason: this.LoanAppForm.loanReason,
              loanStatement: this.LoanAppForm.loanStatement
            })
            .then(({ status, message = "" }) => {
              if (status == 200) {
                this.$message({
                  message,
                  type: "success"
                });
                this.dialogVisible = false;
              } else if (status == 401) {
                this.$message({
                  message,
                  type: "error"
                });
              }
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          return false;
        }
      });
    },
    getLoanSchedule() {
      this.$axios
        .post("/loan/getLoanSchedule", {
          userLoginAccount: sessionStorage.getItem("userLoginAccount")
        })
        .then(({ status, message = "", contractId = "" }) => {
          if (status == 200) {
            this.contractId = contractId;
            this.dialogVisible2 = true;
          } else if (status == 401) {
            this.$message({
              message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    onConfirm() {
      this.stepsActive++;
      // let that = this;
      // setTimeout(() => {
      //   that.$axios
      //     .post("/loan/updateLoanProgress", {
      //       contractId: that.contractId
      //     })
      //     .then(({ status }) => {
      //       if (status == 200) {
      //         this.stepsActive++;
      //       } else if (status == 401) {
      //         that.$message({
      //           message: "修改失败",
      //           type: "error"
      //         });
      //       }
      //     })
      //     .catch(error => {
      //       console.log(error);
      //     });
      // },5000);
    },
    exportFile() {
      window.open(
        `http://106.15.203.32:9999/user/downloadFile/${sessionStorage.getItem(
          "userLoginAccount"
        )}的助学贷款申请表.pdf`
      );
    }
  }
};
</script>

<style lang="scss" scoped>
.main {
  padding: 10px 30px;
  .leftMain {
    display: flex;
    flex-direction: column;
    padding: 0 19px;
    .el-button {
      margin: 0;
      height: 4rem;
      font-size: 20px;
      &:nth-of-type(2) {
        margin: 2rem 0;
      }
    }
  }
  .rightMain {
    .col6 {
      display: flex;
      flex-direction: column;
      .el-button {
        height: 7rem;
        font-size: 20px;
        margin: 0;
        &:nth-of-type(1) {
          margin-bottom: 2rem;
        }
      }
      .el-button--green {
        background: #3cc !important;
        color: #fff;
        &:hover {
          opacity: 0.6;
        }
      }
    }
  }
  .bottom {
    margin-top: 5rem;
    /deep/ .el-card__body {
      display: flex;
      height: 9rem;
      .cardLeft {
        i {
          font-size: 2.5rem;
          color: #99a0a9;
        }
      }
      .cardRight {
        padding-left: 20px;
        text-align: left;
        font-size: 14px;
        .cardTitle {
          font-size: 18px;
        }
        .el-button {
          width: 310px;
          margin: 30px;
        }
      }
    }
  }
  .stepContent {
    margin-top: 30px;
    height: 100px;
    color: rgb(212, 149, 55);
    .two {
      font-size: 20px;
      text-align: center;
      font-weight: 600;
      line-height: 90px;
      i {
        font-size: 25px;
      }
    }
  }
}
</style>
